<template>
  <div class="order">
    <div class="header">
      <van-nav-bar
        title="我的收藏"
        left-text="返回"
        left-arrow
        fixed
        @click-left="back"
      />

    </div>
    <Bg>
      <div class="sc-content" v-for="(item,index) in AllSelectionData" :key="index" @click="delOrDet(item.pid,$event,index)">
        <Product :pro="item">
          <template #del>
              <van-icon class="del" name="delete-o"  />
          </template>
        </Product>
      </div>
    </Bg>
  </div>
</template>

<script>
import Bg from "../components/Bg.vue";
import Product from "../components/Product.vue";
export default {
  name: "ShouCang",
  data(){
      return{
        AllSelectionData:[]
      }
  },
  components:{
    Bg,
    Product
  },
  created(){
     this.getSelectionData();
  },
  methods:{
    back(){
      this.$router.go(-1)
    },
    delOrDet(pid,event,index){
      // e.target 当前点击的元素
      // e.currentTarget 绑定事件的元素
      // 判断==  赋值=
      if(event.target.className == "auto-img"){
        // console.log(111);
        this.toDetail(pid);
        // 事件源匹配类名时,需要写全
      }else if(event.target.className == "del van-icon van-icon-delete-o"){
        this.delectSelectionData(pid,index)
        // console.log(222);
      }
    },
    toDetail(pid){
        this.$router.push({name: "Detail", params: {pid}});
    },
    // 删除收藏商品
    delectSelectionData(pid, index){
      //获取token
      let tokenString = this.$cookies.get('TOKEN');

      this.axios({
        method: "POST",
        url: "/notlike",
        data: {
          pid,
          tokenString
        }
      })
        .then((res) => {
          console.log("getSelectionData res==>",res);
          if (res.data.code === 900) {
            // splice删除指定下标元素
            this.AllSelectionData.splice(index,1);
          }
        
        })
        .catch((err) => {
            console.log(err);
        });
    },
    getSelectionData(){
        //获取token
      let tokenString = this.$cookies.get('TOKEN');

      this.axios({
        method: "GET",
        url: "/findAllLike",
        params: {
          tokenString
        }
      })
        .then((res) => {
          console.log("getSelectionData res==>",res);
          if (res.data.code === 2000) {
            this.AllSelectionData = res.data.result;
          }
        
        })
        .catch((err) => {
          
        });
    }
  }
}
</script>


<style lang="less" scoped>
.order {
  /deep/ .header > .van-nav-bar .van-icon{
      margin-left: 0;
  }
  /deep/ .van-nav-bar .van-icon {
    color: #eb2403;
  }

  /deep/ .van-nav-bar__text {
    color: #eb2403;
  }
  /deep/ .bg .bg-content[data-v-00f2d816]{
    display: flex;
     flex-wrap: wrap;
  }
  /deep/ .sc-content{
    width:calc(~"30%");
    margin-left: 9px;
    margin-top: 10px;
  }
  /deep/ .product .pro-enname[data-v-3cf4ef6f]{
    padding: 5px 0;
  }
  /deep/ .product .pro-price[data-v-3cf4ef6f]{
    color:#eb2403;
  }
  /deep/ .van-icon{
    margin-left: 45px;
    color: #c0c0c0;
  }
  /deep/ .product .pro-zhname[data-v-3cf4ef6f]{
    color: #6e6a67;
  }
}
</style>